import React from 'react'
import { observer, inject } from 'mobx-react'
import JsUtil from 'tsx/common/JsUtil'
import cnst from 'tsx/common/Constant'
import DivView from 'tsx/view/DivView'
import BrView from 'tsx/view/BrView'
import RowLayout from 'tsx/layout/RowLayout'
import TaroModal from 'tsx/component/TaroModal'
import LegenImage from 'tsx/view/LegenImage'
import CascadeBox from 'tsx/form/CascadeBox'
import IntegerInput from 'tsx/form/IntegerInput'
import TextLabel from 'tsx/view/TextLabel'

type PageStateProps = {
	store: {
		orderStore: {
			b2bGlobalQty: [],
			b2bGlobalSpecIds: [],
			b2cGlobalQty: [],
			b2cGlobalSpecIds: [],
			B2bRegionQty: [],
			B2bRegionSpecIds: [],
			b2cRegionQty: [],
			b2cRegionSpecIds: [],
			mallType: number,
			setB2cGlobalQty: Function
		}
	}
}

interface ShoppingCartDetail {
	props: PageStateProps
}

@inject('store')
@observer
class ShoppingCartDetail extends React.Component {
	constructor(props) {
		super(props)

		this.state = {
			checkedList: [],
			checkedList2: [],
			quantityList2: [],
			messages: {}
		}
	}

	componentDidMount() {
		console.log('ShoppingCartDetail componentDidMount')
		this.fetchShopingCart();
	}

	componentDidUpdate(preProps, preState, spanshot) {
		if (this.props.mallType != preProps.mallType || this.props.refreshDetail != preProps.refreshDetail) {
			this.fetchShopingCart();
		}

		const checkAll = this.props.checkAll;
		if (checkAll != preProps.checkAll) {
			if (checkAll == 0) {
				this.setState({ checkedList: [], checkedList2: [] }, () => this.save2Store());
			} else if (this.checkedAllList) {
				this.getQty(this.records)
				this.setState({ checkedList: this.checkedAllList, checkedList2: this.checkedAllList2 }, () => this.save2Store());
			}
		}
	}

	memberId = JsUtil.getAppItem('currUserId');
	records = []
	checkedAllList = []
	checkedAllList2 = []

	fetchShopingCart() {
		let path = ''
		if (this.props.mallType == cnst.MALL_TYPE_G_B2B) {
			path = 'b2b/global'
		} else if (this.props.mallType == cnst.MALL_TYPE_G_B2C) {
			path = 'b2c/global'
		} else if (this.props.mallType == cnst.MALL_TYPE_R_B2B) {
			path = 'b2b/region'
		} else if (this.props.mallType == cnst.MALL_TYPE_R_B2C) {
			path = 'b2c/region'
		}

		const succ = (result) => {
			if (JsUtil.handleMessage(this, result)) {
				this.records = result.body

				// this.setState({ [name]2: checkedList2, quantityList2: quantityList2 });
				this.setState({ quantityList2: this.getQty(this.records) });
			}
		}

		const err = (result) => {
			JsUtil.handleMessage(this, result, true, true)
		}

		JsUtil.asyncHttpGet(this, `/cm/pub/${path}/cart/2/search?memberId=${this.memberId}`, succ, err)
	}

	getQty = (records) => {
		const checkedList = []
		const checkedList2 = []
		const quantityList2 = []

		records.map((item, idx) => {
			const checkedList22 = []
			const quantityList22 = []
			item.second.map((item2, idx2) => {
				checkedList22.push(item2.specId)
				quantityList22.push(item2.quantity)
			})

			checkedList2.push([...checkedList22])
			quantityList2.push(quantityList22)
			checkedList.push('' + idx)
		})

		this.checkedAllList = checkedList
		this.checkedAllList2 = checkedList2

		return quantityList2;
	}

	handleQuantity = (idx, idxp) => (name, value) => {
		const preQuantity2 = this.state.quantityList2

		preQuantity2[idxp][idx] = value

		this.setState({ quantityList2: preQuantity2 });

		this.save2Store()
	}

	initCartStore = (mallType) => {
		const { orderStore } = this.props.store
		if (mallType == cnst.MALL_TYPE_G_B2B) {
			orderStore.setMallType(cnst.MALL_TYPE_G_B2B)
		} else if (mallType == cnst.MALL_TYPE_G_B2C) {
			orderStore.setMallType(cnst.MALL_TYPE_G_B2C)
		} else if (mallType == cnst.MALL_TYPE_R_B2B) {
			orderStore.setMallType(cnst.MALL_TYPE_R_B2B)
		} else if (mallType == cnst.MALL_TYPE_R_B2C) {
			orderStore.setMallType(cnst.MALL_TYPE_R_B2C)
		}

		orderStore.setB2bGlobalSpecIds([])
		orderStore.setB2bGlobalQty([])

		orderStore.setB2cGlobalSpecIds([])
		orderStore.setB2cGlobalQty([])

		orderStore.setB2bRegionSpecIds([])
		orderStore.setB2bRegionQty([])

		orderStore.setB2cRegionSpecIds([])
		orderStore.setB2cRegionQty([])
	}

	save2Store = () => {
		const mallType = this.props.mallType
		this.initCartStore(mallType)

		const checkedList2 = this.state.checkedList2
		const quantityList2 = this.state.quantityList2
		const checkedAllList2 = this.checkedAllList2
		const selectedSpecIds = []
		const selectedSpecQty = []
		checkedAllList2.map((item, indx) => {
			item.map((item2, indx2) => {
				if (JsUtil.containEleValue(checkedList2, item2)) {
					selectedSpecIds.push(item2)
					selectedSpecQty.push(quantityList2[indx][indx2])
				}
			})
		})

		const { orderStore } = this.props.store

		if (mallType == cnst.MALL_TYPE_G_B2B) {
			orderStore.setB2bGlobalSpecIds(selectedSpecIds)
			orderStore.setB2bGlobalQty(selectedSpecQty)
			orderStore.setMallType(cnst.MALL_TYPE_G_B2B)
		} else if (mallType == cnst.MALL_TYPE_G_B2C) {
			orderStore.setB2cGlobalSpecIds(selectedSpecIds)
			orderStore.setB2cGlobalQty(selectedSpecQty)
			orderStore.setMallType(cnst.MALL_TYPE_G_B2C)
		} else if (mallType == cnst.MALL_TYPE_R_B2B) {
			orderStore.setB2bRegionSpecIds(selectedSpecIds)
			orderStore.setB2bRegionQty(selectedSpecQty)
			orderStore.setMallType(cnst.MALL_TYPE_R_B2B)
		} else if (mallType == cnst.MALL_TYPE_R_B2C) {
			orderStore.setB2cRegionSpecIds(selectedSpecIds)
			orderStore.setB2cRegionQty(selectedSpecQty)
			orderStore.setMallType(cnst.MALL_TYPE_R_B2C)
		}

		this.props.setSelectedSpecIds(selectedSpecIds);
	}

	getCommodityOptions = (records) => {
		const options = []
		const quantityList2 = this.state.quantityList2

		records.map((item, indx) => {
			const options2 = []
			item.second.map((item2, indx2) => {
				options2.push({
					idxp: indx, value: item2.specId,
					label: (<RowLayout><LegenImage src={JsUtil.IMAGE_PREFIX + item2.commodityImageUri} width={25} height={25} /><TextLabel width={-75}>{item2.commodityName}</TextLabel></RowLayout>),
					desc: (<RowLayout justifyContent='space-around' width={cnst.NUM_WIDTH_MAIN}><TextLabel color='red' >{JsUtil.currFormat(item2.specPrice)}</TextLabel><IntegerInput name={indx + '-' + indx2} min={1} max={999} value={quantityList2[indx][indx2]} onChange={this.handleQuantity(indx2, indx)} /></RowLayout>)
				})
			})


			const checkedList2 = this.state.checkedList2
			const checkedList2i = checkedList2.length > 0 ? checkedList2[indx] : [];
			const desc = (
				<CascadeBox name={'checkedList2-' + indx}
					options={options2}
					value={checkedList2i}
					onChange={this.onCheckBox2Changed}
					idxp={indx}
				>
				</CascadeBox>
			)

			options.push({ idxp: 0, value: '' + indx, label: <TextLabel alignItems='center'>{item.first}</TextLabel>, desc: desc })
		})

		return options;
	}

	onCheckBox1Changed = (name, selectedValues, changedValue, idx, idxp) => {
		const preValue = this.state.checkedList
		const preValue2 = this.state.checkedList2
		this.getQty(this.records);
		const checkedAllList2 = this.checkedAllList2
		if (selectedValues.find(e => e == changedValue) == undefined) preValue2[idx] = []
		else preValue2[idx] = checkedAllList2[idx]

		this.setState({ [name]: selectedValues, checkedList2: preValue2 }, () => this.save2Store());
	}

	onCheckBox2Changed = (name, selectedValues, changedValue, idx, idxp) => {
		let preValue = this.state.checkedList
		const preValue2 = this.state.checkedList2

		preValue2[idxp] = selectedValues
		const length = this.checkedAllList2[idxp].length
		if (selectedValues.length < length) {
			let isExists = false
			preValue.map((item, indx) => {
				if (item == idxp) isExists = true;
			})

			if (isExists) preValue = JsUtil.delEleByValue(preValue, '' + idxp);
		} else if (selectedValues.length == length) {
			let isExists = false;
			preValue.map((item, indx) => {
				if (item == idxp) isExists = true;
			})
			if (!isExists) preValue.push('' + idxp);
		}

		this.setState({ checkedList: preValue, checkedList2: preValue2 }, () => this.save2Store())
	}

	onConfirm = () => {
		JsUtil.closeMsgBox(this)
	}

	render() {
		const options = this.getCommodityOptions(this.records)

		return (
			<DivView>
				<CascadeBox name='checkedList'
					options={options}
					value={this.state.checkedList}
					onChange={this.onCheckBox1Changed}
				>
				</CascadeBox>
				<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_TRANS_WHITE} />
				<TaroModal isOpened={this.state.messages.isOpened} messages={this.state.messages}></TaroModal>
			</DivView>
		)
	}
}

export default ShoppingCartDetail;